import { Component, Prop, Vue } from "vue-property-decorator";


import { FirestoreService } from '@/services/FirestoreService';
import { storage } from '@/firebaseInit';
import GiftCardItemInfo from "@/dto/GiftCardItemInfo";
import GiftCardItem from "@/dto/GiftCardItem";
import LoginImageInfo from "@/dto/LoginImageInfo";


@Component
export default class LoginImageSection extends Vue {

  loading = false;
  label = "Login/Register Image";
  loginImageInfo: LoginImageInfo = new LoginImageInfo();
  selectedImage: any = null;
  selectedHeaderImage = '';
  currentLoginImageInfo: LoginImageInfo = new LoginImageInfo();
  uploadHeaderBtnDisabled = true;
  uploadHeaderBtnLoading = false;
  saveItem = false;
  carouselItem = false;
  editId = "";
  deleteId = ""
  dialogDelete = false;
  async mounted() {
    await this.getGiftCardDetails()
  }

  async validateForm() {
    if ((this.$refs.addSellerForm as Vue & { validate: () => boolean }).validate()) {
      if (this.currentLoginImageInfo.imageUrl !== '') {
        this.saveCarousel();
      } else {
        Vue.toasted.show('Please add header image ', { icon: 'face', type: 'error' });
      }
    }
  }
  resetForm() {
    this.currentLoginImageInfo = new LoginImageInfo();
    (this.$refs.addSellerForm as Vue & { reset: () => boolean }).reset();
  }

  displayDeleteModal(item: any) {
    this.deleteId = item;
    this.dialogDelete = true;
  }
  closeDelete() {
    this.dialogDelete = false;
  }
  async saveCarousel() {
    try {
      this.saveItem = true;

      const snapshot = await FirestoreService.setDocument('HomeScreen', 'loginImage', this.currentLoginImageInfo, LoginImageInfo.converter);
      Vue.toasted.show('Added Item Successfully', { icon: 'face', type: 'success' });
      this.selectedImage = null;
      this.selectedHeaderImage = '';
      this.carouselItem = false;
      this.resetForm();

    } catch (error) {
      Vue.toasted.show('Error fetching Gift Card Details', { icon: 'face', type: 'error' });
      console.log(error)
    }
    finally {
      this.saveItem = false;
    }
  }
  previewImage(event: any) {
    this.selectedImage = event.target.files[0];
    this.selectedHeaderImage = URL.createObjectURL(this.selectedImage);
    this.uploadHeaderBtnDisabled = false;
  }

  addGiftCardItem(item: any) {
    console.log(item)
    if (item && item !== '') {
      this.editId = item;
      const cItem: any = this.loginImageInfo;
      this.currentLoginImageInfo = cItem;
      this.selectedHeaderImage = this.currentLoginImageInfo.imageUrl;
      this.carouselItem = true;
    } else {
      this.selectedHeaderImage = '';
      this.editId = '';
      this.carouselItem = true;
    }

  }
  cancelForm() {
    this.resetForm();
    this.carouselItem = false;
  }
  async getGiftCardDetails() {
    try {
      this.loading = true;
      const snapshot = await FirestoreService.getDocument('HomeScreen', 'loginImage', LoginImageInfo.converter);
      if (snapshot !== null) {
        this.loginImageInfo = snapshot.data() as LoginImageInfo;
      } else {
        Vue.toasted.show('No Saved Details found', { icon: 'face', type: 'error' });
      }

    } catch (error) {
      Vue.toasted.show('Error fetching Login/Register Image Details', { icon: 'face', type: 'error' });
      console.log(error)
    }
    finally {
      this.loading = false;
    }

  }
  getFileExtension(selectedImage: any) {
    return selectedImage.name.substring(selectedImage.name.lastIndexOf(".") + 1);
  }
  async uploadHeaderImage() {
    try {
      this.uploadHeaderBtnLoading = true;
      const fileExtension = this.getFileExtension(this.selectedImage);
      const metadata = {
        cacheControl: 'public,max-age=172800',
      };
      const resp = await storage.ref(`SITE/loginPlaceholderImage.` + fileExtension).put(this.selectedImage, metadata);
      const url = await resp.ref.getDownloadURL();
      this.currentLoginImageInfo.imageUrl = url;
      Vue.toasted.show('Image Uploaded Successfully', { icon: 'face', type: 'success' });
    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });
    } finally {
      this.uploadHeaderBtnLoading = false;
    }


  }

}
