import { Component, Prop, Vue, Watch } from "vue-property-decorator";

import AddressPopUp from "@/components/AddressPopUp/AddressPopUp.vue";
import { FirestoreService } from "@/services/FirestoreService";
import { auth, firestore } from "@/firebaseInit";
import { AuditFields } from "@/dto/AuditFields";

import Swal from "sweetalert2";
import InventoryItem from "@/dto/InventoryItem";
import JsBarcode from "jsbarcode";
import { TagTemplate } from "@/templates/TagTemplate";
import { PrintTemplate } from "@/templates/PrintTemplateNew";
import { saveAs } from "file-saver";
import { InventoryTransactions } from "@/dto/InventoryTransactions";
@Component({
  components: {
    AddressPopUp,
  },
})
export default class Inventory extends Vue {
  inventoryItem = new InventoryItem();
  inventoryTransactions: InventoryTransactions[] = [];
  isShippingAddress = false;
  edit = false;
  printDialog = false;
  loading = false;
  loadTransactions = false;
  transactionsAvailable = true;
  taxRates = [0, 0.1, 0.25, 1, 1.5, 3, 5, 7.5, 12, 18, 28];
  itemTypes = ["New", "Used", "Refurbished"];
  invoiceDateMenu = false;
  dueDateMenu = false;
  billingAddressFlag = false;
  billEdit = false;
  label = "New Inventory Item";
  copiesToPrint = 1;
  dataCopies: string[] = [];

  receiptTypes: any[] = [
    "Original for Recipient",
    "Duplicate for Transporter",
    "Triplicate for Supplier",
  ];
  dummyLabel = 0;
  updateBarCode() {
    JsBarcode(this.$refs.barCode, this.inventoryItem.itemCode, {
      height: 30,
      width: 1,
      fontSize: 8,
      displayValue: true,
      textMargin: 0,
      marginBottom: 1,
      margin: 0,
    });
  }
  async mounted() {
    console.log(this.$route.query.id);
    if (this.$route.query.id) {
      this.edit = true;
      try {
        await this.getInventory();
      } catch (error) {
        Vue.toasted.show(error.message, { icon: "cross", type: "error" });
      }
    } else {
      this.edit = false;
      this.inventoryItem = new InventoryItem();
    }

    this.label = this.edit ? "Edit Inventory Item" : "New Inventory Item";
  }
  private async getInventory() {
    const invoiceData = await FirestoreService.getSingleDocument(
      "Inventory",
      this.$route.query.id as string,
      InventoryItem.converter
    );
    this.inventoryItem = invoiceData.data();
    this.updateBarCode();
    await this.getTransactions();
  }
  async loadNextTransactions() {
    this.loadTransactions = true;
    try {
      const transactionItems: InventoryTransactions[] = [];

      const inventoryTransactions =
        await FirestoreService.getDocumentsByOrderWithStartKey(
          `Inventory/${this.inventoryItem.itemCode}/transactions/`,
          InventoryTransactions.converter,
          "txnId",
          this.inventoryTransactions[this.inventoryTransactions.length - 1]
            .txnId,
          "desc",
          20
        );
      if (!inventoryTransactions.empty) {
        inventoryTransactions.forEach((doc: any) => {
          const transData = doc.data() as InventoryTransactions;
          transData.txnId = new Date(
            Number.parseInt(transData.txnId)
          ).toLocaleString();
          transactionItems.push(transData);
        });
        if (transactionItems.length < 20) {
          this.transactionsAvailable = false;
        } else {
          this.transactionsAvailable = true;
        }
        this.inventoryTransactions.push(...transactionItems);
      }
    } catch (error) {
    } finally {
      this.loadTransactions = false;
    }
  }
  private async getTransactions() {
    const inventoryTransactions = await FirestoreService.getDocumentsByOrder(
      `Inventory/${this.inventoryItem.itemCode}/transactions/`,
      InventoryTransactions.converter,
      "txnId",
      "desc",
      20
    );
    if (!inventoryTransactions.empty) {
      this.inventoryTransactions = [];
      inventoryTransactions.forEach((doc: any) => {
        const transData = doc.data() as InventoryTransactions;
        transData.txnIdDate = new Date(
          Number.parseInt(transData.txnId)
        ).toLocaleString();
        this.inventoryTransactions.push(transData);
      });
      if (this.inventoryTransactions.length < 20) {
        this.transactionsAvailable = false;
      } else {
        this.transactionsAvailable = true;
      }
      //Vue.toasted.show('Search complete', { icon: 'face', type: 'success' });
    } else {
      // Vue.toasted.show('No results found', { icon: 'face', type: 'error' });
    }
    return inventoryTransactions;
  }

  async validateForm() {
    if (
      (
        this.$refs.addInvoiceForm as Vue & {
          validate: () => boolean;
        }
      ).validate()
    ) {
      if (this.edit) {
        await this.updateInvoice();
      } else {
        await this.addInvoice();
      }
    }
  }
  async validatePrintForm() {
    if (
      (
        this.$refs.printForm as Vue & {
          validate: () => boolean;
        }
      ).validate()
    ) {
      await this.printItemV2();
    }
  }
  async printItemV2() {
    try {
      this.dataCopies = [];

      for (
        let i = this.inventoryItem.lastLabelPrinted + 1;
        i <=
        Number(this.copiesToPrint) +
          Number(this.inventoryItem.lastLabelPrinted);
        i++
      ) {
        this.dataCopies.push(
          PrintTemplate.getMessage({
            barcode: this.inventoryItem.itemCode,
            tagName: this.inventoryItem.itemName,
            itemCondtion: this.inventoryItem.itemCondtion,
            count: i,
          })
        );
      }
      console.log(this.dataCopies);

      const updatedLabels =
        this.inventoryItem.lastLabelPrinted + Number(this.copiesToPrint);
      console.log(updatedLabels, typeof updatedLabels);
      await FirestoreService.setOrMergeDocument(
        "Inventory",
        this.inventoryItem.itemCode,
        {
          lastLabelPrinted: updatedLabels,
        },
        InventoryItem.converter
      );
      // const file: File = new File(
      //   this.dataCopies.join("\n"),
      // `${this.inventoryItem.itemCode}-${Number(
      //   this.inventoryItem.lastLabelPrinted
      // )}-${
      //   Number(this.copiesToPrint) +
      //   Number(this.inventoryItem.lastLabelPrinted)
      // }-labels.prn`
      // );
      const blob = new Blob([this.dataCopies.join("\n")], {
        type: "text/plain;charset=utf-8",
      });

      saveAs(
        blob,
        `${this.inventoryItem.itemCode}-${Number(
          this.inventoryItem.lastLabelPrinted
        )}-${
          Number(this.copiesToPrint) +
          Number(this.inventoryItem.lastLabelPrinted)
        }-labels.prn`
      );
      await this.getInventory();
      this.printDialog = false;
    } catch (error) {
      console.log(error);
      Vue.toasted.show(error.message, { icon: "cross", type: "error" });
    }
  }
  async printItem() {
    try {
      const newWindow = window.open("", "PrintWindow");
      const dataCopies = [];
      const data: any = this.$refs.barCodeContent;
      console.log(this.copiesToPrint, typeof this.copiesToPrint);

      for (let i = 0; i < this.copiesToPrint; i++) {
        dataCopies.push(data.innerHTML);
      }
      console.log(dataCopies);

      const finData = TagTemplate.getMessage({
        tagDiv: dataCopies,
        tagName: this.inventoryItem.itemName,
        lastLabelPrinted: this.inventoryItem.lastLabelPrinted,
        copiesToPrint: this.copiesToPrint,
      });

      const updatedLabels =
        this.inventoryItem.lastLabelPrinted + Number(this.copiesToPrint);
      console.log(updatedLabels, typeof updatedLabels);
      await FirestoreService.setOrMergeDocument(
        "Inventory",
        this.inventoryItem.itemCode,
        {
          lastLabelPrinted: updatedLabels,
        },
        InventoryItem.converter
      );

      if (newWindow) {
        newWindow.onload = () => {
          newWindow.document.getElementsByTagName(
            "html"
          )[0].style.backgroundColor = "whiteSmoke";
        };
        newWindow.document.writeln(finData);
        newWindow.document.close();
      }
      this.printDialog = false;
      await this.getInventory();
    } catch (error) {
      Vue.toasted.show(error.message, { icon: "cross", type: "error" });
    }
  }
  async addInvoice() {
    try {
      this.loading = true;

      const dateNow = new Date();
      const auditfield: AuditFields = {
        createdBy: {
          uid: auth.currentUser?.uid,
          name: auth.currentUser?.displayName,
          email: auth.currentUser?.email,
        },
        createdOn: dateNow.getTime(),
      };
      this.inventoryItem.auditTrail = auditfield;

      await firestore.runTransaction(async (t) => {
        const currData = await t.get(
          firestore.collection("Inventory").doc(this.inventoryItem.itemCode)
        );
        if (!currData.exists) {
          t.set(
            firestore.collection("Inventory").doc(this.inventoryItem.itemCode),
            InventoryItem.converter.toFirestore(this.inventoryItem),
            { merge: true }
          );
          const timestamp = dateNow.getTime().toString();
          t.set(
            firestore
              .collection("Inventory")
              .doc(this.inventoryItem.itemCode)
              .collection("transactions")
              .doc(timestamp),
            {
              txnId: timestamp,
              txnType: "CREDIT",
              qty: this.inventoryItem.availableQuantity,
              availableQty: this.inventoryItem.availableQuantity,
            }
          );
          t.set(firestore.collection("InventoryTransactions").doc(timestamp), {
            itemCode: this.inventoryItem.itemCode,
            txnId: timestamp,
            txnType: "CREDIT",
            qty: this.inventoryItem.availableQuantity,
            availableQty: this.inventoryItem.availableQuantity,
          });
        } else {
          Vue.toasted.show("Item code Already exists", {
            icon: "cross",
            type: "error",
          });
        }
      });
      Vue.toasted.show("Invoice Added Successfully", {
        icon: "face",
        type: "success",
      });
      await this.getTransactions();
      await this.showAfterSaveDialog();
    } catch (error) {
      console.log(error);
      Vue.toasted.show(error.message, { icon: "cross", type: "error" });
    } finally {
      this.loading = false;
    }
  }
  async updateInvoice() {
    try {
      this.loading = true;
      const dateNow = new Date();
      const auditfield: AuditFields = {
        createdOn: this.inventoryItem.auditTrail.createdOn,
        createdBy: this.inventoryItem.auditTrail.createdBy,
        updatedBy: {
          uid: auth.currentUser?.uid,
          name: auth.currentUser?.displayName,
          email: auth.currentUser?.email,
        },
        updatedOn: dateNow.getTime(),
      };
      this.inventoryItem.auditTrail = auditfield;

      await firestore.runTransaction(async (t) => {
        const currData = await t.get(
          firestore.collection("Inventory").doc(this.inventoryItem.itemCode)
        );
        if (currData.exists) {
          const currInventory = currData.data() as InventoryItem;
          const updatedQuantity = Math.abs(
            currInventory.availableQuantity -
              this.inventoryItem.availableQuantity
          );

          t.set(
            firestore.collection("Inventory").doc(this.inventoryItem.itemCode),
            InventoryItem.converter.toFirestore(this.inventoryItem),
            { merge: true }
          );
          const timestamp = dateNow.getTime().toString();
          if (updatedQuantity != 0) {
            t.set(
              firestore
                .collection("Inventory")
                .doc(this.inventoryItem.itemCode)
                .collection("transactions")
                .doc(timestamp),
              {
                txnId: timestamp,
                txnType:
                  currInventory.availableQuantity >
                  this.inventoryItem.availableQuantity
                    ? "DEBIT"
                    : "CREDIT",
                qty: updatedQuantity,
                availableQty: this.inventoryItem.availableQuantity,
              }
            );
          }
        } else {
          Vue.toasted.show("Item does not exists", {
            icon: "cross",
            type: "error",
          });
        }
      });

      Vue.toasted.show("Invoice updated Successfully", {
        icon: "face",
        type: "success",
      });
      await this.getTransactions();
    } catch (error) {
      console.log(error);
      Vue.toasted.show(error.message, { icon: "cross", type: "error" });
    } finally {
      this.loading = false;
    }
  }

  async resetForm() {
    (
      this.$refs.addInvoiceForm as Vue & {
        resetValidation: () => boolean;
      }
    ).resetValidation();

    this.inventoryItem = new InventoryItem();
    JsBarcode(this.$refs.barCode, " ");
  }

  cancelEdit() {
    this.$store.dispatch("setInvoicePanelFlag", false);
  }

  headers: any = [
    {
      text: "Timestamp",
      value: "txnIdDate",
      fixed: true,
      width: "150px",
      divider: true,
    },
    {
      text: "transaction Type",
      value: "txnType",
      fixed: true,
      width: "100px",
      divider: true,
    },
    {
      text: "transaction Quantity",
      value: "qty",
      fixed: true,
      width: "100px",
      divider: true,
    },
    {
      text: "Available Quantity",
      value: "availableQty",
      fixed: true,
      width: "100px",
      divider: true,
    },
    {
      text: "OrderId",
      value: "orderId",
      fixed: true,
      width: "100px",
      divider: true,
    },
  ];

  openPrintDialog() {
    this.printDialog = true;
    this.copiesToPrint = this.inventoryItem.availableQuantity;
  }
  closePrintDialog() {
    this.printDialog = false;
  }
  async showAfterSaveDialog() {
    try {
      const res = await Swal.fire({
        title: "Invoice Saved",
        text: "Do you want to print label? ",
        icon: "success",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes",
        cancelButtonText: "No",
      });
      if (res.isConfirmed) {
        this.edit = true;
        this.openPrintDialog();
      } else {
        this.resetForm();
      }
    } catch (error) {
      Vue.toasted.show(error.message, { icon: "face", type: "error" });
    }
  }
}
