import 'es6-shim';
import 'reflect-metadata';
import { v4 as uuidv4 } from 'uuid';
import GiftCardItem from './GiftCardItem';
import { classToPlain, plainToClass, Type } from 'class-transformer';

export default class LoginImageInfo {
    imageUrl: string;
    description: string;
    public constructor() {
        this.imageUrl = '';
        this.description = '';
    }

    static converter = {
        toFirestore(expense: LoginImageInfo): firebase.firestore.DocumentData {
            return classToPlain(expense);
        },
        fromFirestore(
            snapshot: firebase.firestore.QueryDocumentSnapshot,
            options: firebase.firestore.SnapshotOptions
        ): LoginImageInfo {
            const data = snapshot.data(options)!;
            return plainToClass(LoginImageInfo, data);
        }
    };
}