import { classToPlain, plainToClass } from 'class-transformer';
import { AuditFields } from './AuditFields';
import { PlatformDetails } from './PlatformDetails';
import ProductImage from './ProductImage';
import SellerDetails from './SellerDetails';

export class ProductInfo {
    id: string;
    name: string;
    description: string;
    auditTrail!: AuditFields;
    genre: string[];
    languages: string[];
    worksOn: string[];
    enabled: boolean;
    inStock: boolean;
    videoId: string;
    hsnNumber: string;
    codAmount: number;
    headerImageUrl: string;
    platformDetails: PlatformDetails[];
    searchPhrase: string;
    sellerDetails: SellerDetails[];
    images: ProductImage[] = [];
    mainCategory: string;
    subCategory: string;
    preOrder = false;
    preOrderDate: string;
    constructor() {
        this.id = "";
        this.name = "";
        this.description = "";
        this.genre = [];
        this.languages = [];
        this.worksOn = [];
        this.enabled = false;
        this.inStock = true;
        this.videoId = "";
        this.headerImageUrl = "";
        this.platformDetails = [];
        this.sellerDetails = [];
        this.searchPhrase = '';
        this.hsnNumber = '';
        this.codAmount = 0;
        this.mainCategory = "";
        this.subCategory = "";
        this.preOrderDate = "";
    }
    static converter = {
        toFirestore(expense: ProductInfo): firebase.firestore.DocumentData {
            return classToPlain(expense);
        },
        fromFirestore(
            snapshot: firebase.firestore.QueryDocumentSnapshot,
            options: firebase.firestore.SnapshotOptions
        ): ProductInfo {
            const data = snapshot.data(options)!;
            return plainToClass(ProductInfo, data);
        }
    };
}