export class PrintTemplate {
  static getMessage(values: any) {
    return `
SIZE 35.50 mm, 50 mm
GAP 3 mm, 0 mm
DIRECTION 0,0
REFERENCE 0,0
OFFSET 0 mm
SET PEEL OFF
SET CUTTER OFF
SET PARTIAL_CUTTER OFF
SET TEAR ON
CLS
BARCODE 249,60,"128M",80,0,90,3,5,"!104${values.barcode}"
CODEPAGE 1252
TEXT 162,152,"ROMAN.TTF",90,1,9,"${values.barcode}"
TEXT 85,179,"ROMAN.TTF",90,1,8,"${values.itemCondtion}"
TEXT 52,187,"ROMAN.TTF",90,1,8,"(${values.count})"
TEXT 130,27,"ROMAN.TTF",90,1,7,"${values.tagName}"
PRINT 1,1
`;
  }
}
