import numWords from "num-words";
export class TagTemplate {
  static getMessage(values: any) {
    return `<!DOCTYPE html>
<html lang="en">
<title>${values.tagName}</title>
<head>
  <meta charset="UTF-8">
  <meta http-equiv="X-UA-Compatible" content="IE=edge">
  <meta name="viewport" content="width=device-width, initial-scale=1.0">
  <title>Bar Code</title>
<style>
.no-uppercase {
  text-transform: none !important;
}

.tagBox{
  border: 1px solid black;
  border-radius: 10px;
  padding: 10px;
  line-height: 90%;
}
.alignLeft{
  margin-top:5px;
  text-justify: center !important;
}
  body{
        width: 21cm;
        height: 29.7cm;
        
        /* change the margins as you want them to be. */
   } 
   body:nth-child(3n+3) { margin-right: 0; }
</style>
</head>

<body id="main" style="display: flex;flex-direction: row;flex-wrap: wrap; justify-content: flex-start; margin: 12mm 6mm 12mm 5mm;align-content: flex-start; align-items: center;">
${values.tagDiv
  .map((element: any, index: number) => {
    return `<div class="tagBox" style="font-size:0.5rem;width:6cm !important;height:2.85cm !important;display: flex;flex-direction: column; justify-content: center;  text-align: center;align-items: center;  font-family: Arial, Roboto, sans-serif;margin-left:2mm;margin-bottom:0.8mm;">
${element} (${values.lastLabelPrinted + index + 1})
</div>`;
  })
  .join("")}


</body>
</html>`;
  }

  static convertToWords(input: any) {
    return numWords(input)
      .toLowerCase()
      .split(" ")
      .map((s: string) => s.charAt(0).toUpperCase() + s.substring(1))
      .join(" ");
  }
}
