import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import { FirestoreService } from '@/services/FirestoreService';
import CarouselInfoV2 from "@/dto/CarouselnfoV2";
import { v4 as uuidv4 } from 'uuid';

import { Section } from "@/dto/Section";
import { ProductInfo } from "@/dto/ProductInfo";


@Component
export default class ProductSection extends Vue {

  loading = false;

  @Prop({ required: true })
  productSection!: Section;

  uploadContentBtnDisabled = true;
  uploadContentBtnLoading = false;
  saveItem = false;
  carouselItem = false;
  sideItem = false;
  editId = "";
  deleteId = ""
  dialogDelete = false;
  searchItems: ProductInfo[] = [];
  sectionItems: ProductInfo[] = [];

  isProductLoading = false;
  selectedProductId = "";
  search = "";
  selectedProduct: ProductInfo = new ProductInfo();

  addProductFlag = false;
  async mounted() {
    await this.getProductsDetails()
  }

  @Watch("search")
  async onPropertyChanged(value: string, oldValue: string) {
    try {
      if (value && value !== "" && value.length >= 3) {
        this.isProductLoading = true;
        const snapshot = await FirestoreService.getdocsStartingWithKey(
          "products",
          value.toLowerCase(),
          "searchPhrase",
          ProductInfo.converter
        );
        if (!snapshot.empty) {
          snapshot.forEach((doc: any) => {
            this.searchItems.push(doc.data());
          });
          //    Vue.toasted.show('Found ' + this.searchItems.length + ' items', { icon: 'face', type: 'success' });
        } else {
          //Vue.toasted.show('No results found', { icon: 'face', type: 'error' });
        }
      }
    } catch (error) {
      Vue.toasted.show(error.message, { icon: "face", type: "error" });
    } finally {
      this.isProductLoading = false;
    }
  }
  onProductChange(item: any) {
    if (item !== null) {
      this.selectedProduct = item as ProductInfo;
      this.addProductFlag = true;
    } else {


    }
  }
  async deleteItem() {
    try {
      const index = this.sectionItems.map((item) => item.id).indexOf(this.deleteId);
      this.sectionItems.splice(index, 1)
      const snapshot = await FirestoreService.deleteDocument(`ProductSections/${this.productSection.id}/products`, this.deleteId);
      Vue.toasted.show('Item Deleted Successfully', { icon: 'face', type: 'success' });
      this.dialogDelete = false;

    } catch (error) {
      Vue.toasted.show('Error deleting Carousel item', { icon: 'face', type: 'error' });
      console.log(error)
    }

  }
  displayDeleteModal(item: any) {
    this.deleteId = item;
    this.dialogDelete = true;
  }
  closeDelete() {
    this.dialogDelete = false;
  }
  async saveProduct() {
    try {
      this.saveItem = true;
      const id = uuidv4();
      this.selectedProduct.id = id;
      this.selectedProduct.auditTrail.createdOn = new Date().getTime();
      const snapshot = await FirestoreService.addDocumentWithId(`ProductSections/${this.productSection.id}/products`, id, this.selectedProduct, ProductInfo.converter);
      await this.getProductsDetails();
      this.selectedProduct = new ProductInfo();
      this.searchItems = [];
      Vue.toasted.show('Added Item Successfully', { icon: 'face', type: 'success' });
      this.addProductFlag = false;
    } catch (error) {
      Vue.toasted.show('Error saving Product Details:' + error, { icon: 'face', type: 'error' });
      console.log(error)
    }
    finally {
      this.saveItem = false;
    }
  }

  addCarouselItem(item: any) {
    console.log(item)

  }

  cancelForm() {
    this.carouselItem = false;
  }
  async validateForm() {
    if ((this.$refs.addSectionForm as Vue & { validate: () => boolean }).validate()) {
      this.saveProduct();
    }
  }
  async getProductsDetails() {
    try {
      this.loading = true;
      this.sectionItems = [];
      const snapshot = await FirestoreService.getDocuments(`ProductSections/${this.productSection.id}/products`, ProductInfo.converter);
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.sectionItems.push(doc.data());
        });
        // Vue.toasted.show('Search complete', { icon: 'face', type: 'success' });

      } else {
        Vue.toasted.show('No results found', { icon: 'face', type: 'error' });
      }

    } catch (error) {
      Vue.toasted.show('Error fetching section Products Details', { icon: 'face', type: 'error' });
      console.log(error)
    }
    finally {
      this.loading = false;
    }

  }
  cancelSectionAddition() {
    //  this.resetForm();
    this.addProductFlag = false;
  }
  getLowestPrice(item: ProductInfo) {
    return item.sellerDetails.find(
      (item) => item.lowestPrice === true
    )?.price;
  }
}
