import 'es6-shim';
import 'reflect-metadata';
import { v4 as uuidv4 } from 'uuid';
import Carousel from './Carousel';
import { classToPlain, plainToClass, Type } from 'class-transformer';

export default class CarouselnfoV2 {
    @Type(() => Carousel)
    carousel: Carousel[] = [];

    @Type(() => Carousel)
    sideItems: Carousel[] = [];
    static converter = {
        toFirestore(expense: CarouselnfoV2): firebase.firestore.DocumentData {
            return classToPlain(expense);
        },
        fromFirestore(
            snapshot: firebase.firestore.QueryDocumentSnapshot,
            options: firebase.firestore.SnapshotOptions
        ): CarouselnfoV2 {
            const data = snapshot.data(options)!;
            return plainToClass(CarouselnfoV2, data);
        }
    };
}