import { Component, Prop, Vue } from "vue-property-decorator";


import { FirestoreService } from '@/services/FirestoreService';
import { storage } from '@/firebaseInit';
import GiftCardItemInfo from "@/dto/GiftCardItemInfo";
import GiftCardItem from "@/dto/GiftCardItem";


@Component
export default class GiftCardSection extends Vue {

  loading = false;
  label = "Popular Gift Cards (Mobile View Only)";
  giftCardInfo: GiftCardItemInfo = new GiftCardItemInfo();
  selectedImage: any = null;
  selectedHeaderImage = '';
  currentGiftCard: GiftCardItem = new GiftCardItem();
  uploadHeaderBtnDisabled = true;
  uploadHeaderBtnLoading = false;
  saveItem = false;
  carouselItem = false;
  editId = "";
  deleteId = ""
  dialogDelete = false;
  async mounted() {
    await this.getGiftCardDetails()
  }

  async validateForm() {
    if ((this.$refs.addSellerForm as Vue & { validate: () => boolean }).validate()) {
      if (this.currentGiftCard.image !== '') {
        this.saveCarousel();
      } else {
        Vue.toasted.show('Please add header image ', { icon: 'face', type: 'error' });
      }
    }
  }
  resetForm() {
    this.currentGiftCard = new GiftCardItem();
    (this.$refs.addSellerForm as Vue & { reset: () => boolean }).reset();
  }
  async deleteItem() {
    try {
      const index = this.giftCardInfo.giftCards.map((item) => item.id).indexOf(this.deleteId);
      this.giftCardInfo.giftCards.splice(index, 1)
      const snapshot = await FirestoreService.setDocument('HomeScreen', 'GiftCards', this.giftCardInfo, GiftCardItemInfo.converter);
      Vue.toasted.show('Item Deleted Successfully', { icon: 'face', type: 'success' });
      this.dialogDelete = false;

    } catch (error) {
      Vue.toasted.show('Error deleting Gift card item', { icon: 'face', type: 'error' });
      console.log(error)
    }

  }
  displayDeleteModal(item: any) {
    this.deleteId = item;
    this.dialogDelete = true;
  }
  closeDelete() {
    this.dialogDelete = false;
  }
  async saveCarousel() {
    try {
      this.saveItem = true;
      if (this.editId !== '') {
        const cItem: any = this.giftCardInfo.giftCards.find(c => c.id === this.editId);
        Object.assign(cItem, this.currentGiftCard);
        if (!cItem) {
          Vue.toasted.show('Error in updating item', { icon: 'face', type: 'error' });
          return;
        }
      } else {
        this.giftCardInfo.giftCards.push(this.currentGiftCard);
      }
      const snapshot = await FirestoreService.setDocument('HomeScreen', 'GiftCards', this.giftCardInfo, GiftCardItemInfo.converter);
      Vue.toasted.show('Added Item Successfully', { icon: 'face', type: 'success' });
      this.selectedImage = null;
      this.selectedHeaderImage = '';
      this.carouselItem = false;
      this.resetForm();

    } catch (error) {
      Vue.toasted.show('Error fetching Gift Card Details', { icon: 'face', type: 'error' });
      console.log(error)
    }
    finally {
      this.saveItem = false;
    }
  }
  previewImage(event: any) {
    this.selectedImage = event.target.files[0];
    this.selectedHeaderImage = URL.createObjectURL(this.selectedImage);
    this.uploadHeaderBtnDisabled = false;
  }

  addGiftCardItem(item: any) {
    console.log(item)
    if (item && item !== '') {
      this.editId = item;
      const cItem: any = this.giftCardInfo.giftCards.find(c => c.id === item);
      this.currentGiftCard = cItem;
      this.selectedHeaderImage = this.currentGiftCard.image;
      this.carouselItem = true;
    } else {
      this.selectedHeaderImage = '';
      this.editId = '';
      this.carouselItem = true;
    }

  }
  cancelForm() {
    this.resetForm();
    this.carouselItem = false;
  }
  async getGiftCardDetails() {
    try {
      this.loading = true;
      const snapshot = await FirestoreService.getDocument('HomeScreen', 'GiftCards', GiftCardItemInfo.converter);
      if (snapshot !== null) {
        this.giftCardInfo = snapshot.data() as GiftCardItemInfo;
      } else {
        Vue.toasted.show('No Saved Details found', { icon: 'face', type: 'error' });
      }

    } catch (error) {
      Vue.toasted.show('Error fetching Gift Cards Details', { icon: 'face', type: 'error' });
      console.log(error)
    }
    finally {
      this.loading = false;
    }

  }
  getFileExtension(selectedImage: any) {
    return selectedImage.name.substring(selectedImage.name.lastIndexOf(".") + 1);
  }
  async uploadHeaderImage() {
    try {
      this.uploadHeaderBtnLoading = true;
      const fileExtension = this.getFileExtension(this.selectedImage);
      const metadata = {
        cacheControl: 'public,max-age=172800',
      };
      const resp = await storage.ref(`GiftCards/${this.currentGiftCard.id}/header.` + fileExtension).put(this.selectedImage, metadata);
      const url = await resp.ref.getDownloadURL();
      this.currentGiftCard.image = url;
      Vue.toasted.show('Image Uploaded Successfully', { icon: 'face', type: 'success' });
    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });
    } finally {
      this.uploadHeaderBtnLoading = false;
    }


  }

}
