import { Component, Prop, Vue } from "vue-property-decorator";


import { FirestoreService } from '@/services/FirestoreService';
import Swal from 'sweetalert2';

import AddCarouselV2 from '@/components/AddCarouselV2/AddCarouselV2.vue';
import ProductSections from '@/components/ProductSections/ProductSections.vue';
import LoginImageSection from '@/components/LoginImageSection/LoginImageSection.vue';


import { ProductInfo } from '@/dto/ProductInfo';
import GiftCardSection from "@/components/GiftCardSection/GiftCardSection.vue";
@Component({
  components: {
    GiftCardSection,
    AddCarouselV2,
    ProductSections, LoginImageSection
  }
})
export default class HomeScreen extends Vue {


  sellerSearchLoading = false;
  sellerList: ProductInfo[] = [];
  loading = false;
  searchDisabled = false;
  queryString = "";
  searchLoading = false;
  headers: any = [
    { text: 'Date Added', value: 'auditTrail.createdOn', },
    { text: 'Product Id', value: 'id' },
    { text: 'Header Image', value: 'headerImageUrl' },
    { text: 'Product Name', value: 'name' },
    { text: 'Added By', value: 'auditTrail.createdBy.email' },
    { text: 'Updated By', value: 'auditTrail.updatedBy.email' },
    { text: 'Actions', value: 'actions', sortable: false },

  ]
  editedIndex = 0;
  editedItem: ProductInfo = new ProductInfo();
  dialog = false;


  async searchProducts() {
    console.log('query:' + this.queryString)
    if (this.queryString !== "") {
      await this.searchQueryString();
    } else {
      await this.getProducts();
    }
  }
  async getProducts() {
    try {
      this.sellerList = [];
      this.sellerSearchLoading = true;
      const snapshot = await FirestoreService.getDocuments('products', ProductInfo.converter);
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.sellerList.push(doc.data());
        });
        Vue.toasted.show('Search complete', { icon: 'face', type: 'success' });

      } else {
        Vue.toasted.show('No results found', { icon: 'face', type: 'error' });
      }


    } catch (error) {
      console.log(error)

    }
    finally {
      this.sellerSearchLoading = false;
    }

  }
  editItem(item: ProductInfo) {
    this.editedIndex = this.sellerList.indexOf(item)
    this.editedItem = Object.assign({}, item)
    this.$store.commit('setCloseSeller', true)
  }


  async deleteItem(item: ProductInfo) {
    try {
      const res = await Swal.fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, delete it!',

      });
      if (res.isConfirmed) {
        await FirestoreService.deleteDocument('products', item.id);
        const deleteIndex = this.sellerList.indexOf(item)

        this.sellerList.splice(deleteIndex, 1)
        Swal.fire(
          'Deleted!',
          'Seller has been deleted.',
          'success'
        )
      }
    } catch (error) {
      Vue.toasted.show(error.message, { icon: 'face', type: 'error' });

    }

  }
  async searchQueryString() {
    try {
      this.searchDisabled = true;
      this.searchLoading = true;
      this.sellerList = [];
      const snapshot = await FirestoreService.getdocsStartingWithKey('products', this.queryString, 'searchPhrase', ProductInfo.converter);
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.sellerList.push(doc.data());
        });
        Vue.toasted.show('Found ' + this.sellerList.length + ' items', { icon: 'face', type: 'success' });

      } else {
        Vue.toasted.show('No results found', { icon: 'face', type: 'error' });
      }

    } catch (error) {
      Vue.toasted.show(error.message, { icon: 'face', type: 'error' });

    } finally {
      this.searchLoading = false;
      this.searchDisabled = false;
      this.queryString = ""
    };
  }
}
