import numWords from 'num-words'
export class InvoiceTemplate {



  static getMessage(values: any) {
    return `<!DOCTYPE html>
<html lang="en">
<title>Invoice ${values.invoiceNumber}</title>
<head>
  <meta charset="UTF-8">
  <meta http-equiv="X-UA-Compatible" content="IE=edge">
  <meta name="viewport" content="width=device-width, initial-scale=1.0">
  <title>Invoice</title>
<style>
		@page {margin: 50px;}
        body {
            margin-top: 0px;
            margin-left: 0px;
        }

        #page_1 {
            position: relative;
            margin-top: 1px;
            margin-right:10px;
            margin-bottom:10px;
            margin-left:10px;
            padding: 0px;
            border: none;
            width: 760px;
        }

.ft0{font: bold 18px 'Helvetica';line-height: 21px;}
.ft1{font: 18px 'Helvetica';line-height: 21px;}.p0{text-align: left;margin-top: 0px;margin-bottom: 0px;}
.ft2{font: bold 15px 'Helvetica';line-height: 18px;}
.ft3{font: 1px 'Helvetica';line-height: 1px;}
.ft4{font: 15px 'Helvetica';line-height: 17px;}
.ft5{font: 15px 'Arial';line-height: 17px;}
.ft6{font: 12px 'Helvetica';line-height: 15px;position: relative; bottom: 6px;}
.ft7{font: 1px 'Helvetica';line-height: 6px;}
.ft8{font: 1px 'Helvetica';line-height: 12px;}
.ft9{font: 1px 'Helvetica';line-height: 7px;}
.ft10{font: bold 12px 'Helvetica';line-height: 15px;}
.ft11{font: bold 12px 'Helvetica';line-height: 15px;position: relative; bottom: -6px;}
.ft12{font: 17px 'Arial';line-height: 15px;}
.ft13{font: 12px 'Helvetica';line-height: 15px;}
.ft14{font: 11px 'Arial';line-height: 14px;}
.ft15{font: 17px 'Arial';line-height: 19px;}
.ft16{font: 12px 'Arial';line-height: 15px;}
.ft17{font: 13px 'Arial';line-height: 16px;}
.ft18{font: bold 17px 'Arial';line-height: 19px;}
.ft19{font: 8px 'Helvetica';color: #808080;line-height: 10px;}
.p0{text-align: right;margin-top: 0px;margin-bottom: 0px;}
.p1{text-align: right;margin-top: 1px;margin-bottom: 0px;}
.p3{text-align: left;margin-top: 0px;margin-bottom: 0px;}

.p21{text-align: right;margin-top: 20px;margin-bottom: 0px;}
.p22{text-align: right;margin-top: 0px;margin-bottom: 20px;}
.p23{text-align: right;margin-top: 2px;margin-bottom: 2px;}
.p24{text-align: left;padding:5px;}
.p25{padding:5px}

</style>
</head>

<body  id="page_1">
<table style="border-collapse: collapse; border-style: hidden;" border="0">
<tbody>
<tr style="">
<td style="width: 50%; "><strong><img src=${values.gstConfig.logoUrl} alt="" /></strong></td>
<td style="width: 50%; ">
<p class="ft0 p0 " style="text-align: right;"><strong>Tax Invoice/Bill of Supply/Cash Memo</strong></p>
<p class="p1 ft1" style="text-align: right;">(${values.receiptHeader})</p>
</td>
</tr>

<tr>
<td style="width: 50%;"><p class="ft4"><strong class="ft2">Sold By:</strong><br/>
${values.gstConfig.name}<br/>
${values.gstConfig.gstAddress.streetAddress}<br/>
${values.gstConfig.gstAddress.city}, ${values.gstConfig.gstAddress.state},  ${values.gstConfig.gstAddress.pinCode}<br/>
IN<br/>
Email ID:  ${values.gstConfig.emailId}<br/>
Mobile No: ${values.gstConfig.mobileNumber}<br/></p>
</td>
<td style="width: 50%;">
<p class="p0 ft4" style="text-align: right;"><strong class="ft2">Billing Address:</strong><br/>
${values.customerName}<br/>
${values.customerGSTIN ? `<span class='ft4'>GSTIN: </span>${values.customerGSTIN}<br/>` : ''}
${values.billingAddress.streetAddress ? `${values.billingAddress.streetAddress}<br/>` : ''}
${values.billingAddress.city ? `${values.billingAddress.city},` : ''}${values.billingAddress.state ? ` ${values.billingAddress.state},` : ''}${values.billingAddress.pinCode ? ` ${values.billingAddress.pinCode}` : ''}<br/>
${values.billingAddress.mobileNumber ? `Mobile: ${values.billingAddress.mobileNumber}` : ''}<br/>
IN<br/>
<span class="ft2" >State/UT Code:&nbsp;</span><span class="ft4">${values.billingAddress.stateCode}</span><br/>

</p>
</td>
</tr>
<tr>
<td style="width: 50%;" VALIGN=TOP>
<p><span class="ft2" >PAN No:&nbsp;</span><span class="ft4">${values.gstConfig.panNumber}</span><br /><span class="ft2">GST Registration No:&nbsp;</span><span class="ft4" >${values.gstConfig.gstNumber}</span></p>
</td>
<td style="width: 50%;">
<p class="p0 ft4" style="text-align: right;"><strong class="ft2">Shipping Address:</strong><br/>
${values.shippingAddress.name ? values.shippingAddress.name : ''}<br/>
${values.shippingAddress.streetAddress ? `${values.shippingAddress.streetAddress}<br/>` : ''}
${values.shippingAddress.city ? `${values.shippingAddress.city},` : ''}${values.shippingAddress.state ? ` ${values.shippingAddress.state},` : ''}${values.shippingAddress.pinCode ? ` ${values.shippingAddress.pinCode}` : ''}<br/>
${values.shippingAddress.mobileNumber ? `Mobile: ${values.billingAddress.mobileNumber}` : ''}<br/>
IN<br/>
<span class="ft2" >State/UT Code:&nbsp;</span><span class="ft4">${values.shippingAddress.stateCode}</span><br/>

</p>

</td>
</tr>
<tr>
<td style="width: 50%;">
<p><span class="ft2" >Due Date:&nbsp;</span><span class="ft4">${values.dueDate}</span></p>
</td>
<td style="width: 50%;">
<p class="p0 ft0" style="text-align: right;"><span class="ft2" >Place of supply:&nbsp;</span><span class="ft4">${values.placeOfSupply.toUpperCase()}</span><br/>
<span class="ft2" >Place of delivery:&nbsp;</span><span class="ft4">${values.placeOfDelivery.toUpperCase()}</span><br/>
<span class="ft2" >Invoice Number:&nbsp;</span><span class="ft4">${values.invoiceNumber}</span><br/>
<span class="ft2" >Invoice Date:&nbsp;</span><span class="ft4">${values.invoiceDate}</span><br/>
</p>
</td>
</tr>
</tbody>
</table>
<p></p>
<table style="border-collapse: collapse; width: 100%; height: 106px;" border="1">
<tbody>
<tr style="height: 36px;" class="ft10">
<td style="width: 5.28726%; height: 18px; background-color: lightgray;">
<p>SR No</p>
</td>
<td style="width: 30.2872%; height: 18px; background-color: lightgray;">Description</td>
<td style="width: 10.8271%; height: 18px; background-color: lightgray;">Unit Price</td>
<td style="width: 5.28721%; height: 18px; background-color: lightgray;">QTY</td>
<td style="width: 13.5258%; height: 18px; background-color: lightgray;">Net Amount</td>
<td style="width: 13.5258%; height: 18px; background-color: lightgray;">Discount</td>
<td style="width: 6.70768%; height: 18px; background-color: lightgray;">Tax Rate</td>
<td style="width: 7.84408%; height: 18px; background-color: lightgray;">Tax Type</td>
<td style="width: 7.70201%; height: 18px; background-color: lightgray;">Tax Amount</td>
<td style="width: 12.5315%; height: 18px; background-color: lightgray;">Total Amount</td>
</tr>
  ${values.invoiceItems.map((invoiceItem: any) =>
      `
                                <tr>
                                  <td class="ft13" style="text-align:center;">
                                    <span>${invoiceItem.srNo}</span>
                                  </td>
                                  <td class="ft13">
                                    <span
                                      >${invoiceItem.itemDesc} ${invoiceItem.hsn ? `| HSN:${invoiceItem.hsn}` : ''} </span
                                    >
                                    ${invoiceItem.serialNumber ? `<span style="color:gray"><br/> S/N: ${invoiceItem.serialNumber} </span>` : ''}
                                  </td>
                                  <td class="ft13" style="text-align:center;">
                                    <span
                                      >${invoiceItem.individualTaxableValueComputed}</span
                                    >
                                  </td>
                                  <td class="ft13" style="text-align:center;">
                                    ${invoiceItem.quantity}
                                  </td>
                                  <td class="ft13" style="text-align:center;">
                                    ${invoiceItem.individualTaxableValueComputedForQty}
                                  </td>
                                  <td class="ft13" style="text-align:center;">
                                    ${invoiceItem.discount > 0 ? `${invoiceItem.discount}%` : 'N\\A'}
                                  </td>
                                  <td class="ft13" style="text-align:center;">
                                    ${invoiceItem.isIgst ? `${invoiceItem.taxableRate}%` : `${invoiceItem.taxableRate / 2}%<br/><br/>${invoiceItem.taxableRate / 2}%`}
                                  </td>
                                  <td class="ft13" style="text-align:center;">
                                    ${invoiceItem.isIgst ? 'IGST' : 'CGST <br/><br/> SGST'}
                                  </td>
                                  <td class="ft13" style="text-align:center;">
                                    ${invoiceItem.isIgst ? `${invoiceItem.igst}` : `${invoiceItem.cgst} <br/><br/> ${invoiceItem.sgst}`}
                                  </td>  <td class="ft13" style="text-align:center;">
                                    ${invoiceItem.total}
                                  </td>
                                </tr>
                                `).join('')}
<tr style="height: 18px;">
<td style="height: 18px;" colspan="8" halign=center><span class="ft2">Total:</span></td>
<td style="width: 7.70201%; height: 18px; background-color: lightgray;text-align:center;" class="ft2">${values.totalTax}</td>
<td style="width: 12.5315%; height: 18px; background-color: lightgray; text-align:center;" class="ft2">${values.totalCost}</td>
</tr>
<tr style="height: 52px;">
<td style="height: 52px;" colspan="10"><p class=" ft2 p3">Amount in words:<br/>
${InvoiceTemplate.convertToWords(values.totalCost)}</p></td>
</tr>
<tr>
<td class="ft13 p24" colspan="8" style="border-right: hidden;">
<p class="ft13 p24">Terms & Conditions <br/>
${values.gstConfig.termsAndConditions}</p>
</td>
<td class="p25" colspan="2">
<p class="p21 ft2">For ${values.gstConfig.name}:</p>
<p class="ft2 p23"><img src=${values.gstConfig.authSignUrl} alt="" /></p>
<p class="p22 ft2">Authorized Signatory</p>
</td>
</tr>
</tbody>
</table>
</body>
</html>`
  }

  static convertToWords(input: any) {
    return numWords(input).toLowerCase().split(' ').map((s: string) => s.charAt(0).toUpperCase() + s.substring(1)).join(' ');

  }

}

