import { Component, Prop, Vue } from "vue-property-decorator";

import store from "@/store";
import { AuditFields } from "@/dto/AuditFields";
import { FirestoreService } from "@/services/FirestoreService";
import Swal from "sweetalert2";
import { UtilityService } from "@/services/UtilityService";
import { triggerThreadSafeValidation } from "vee-validate/dist/types/components/common";
import Inventory from "@/components/Inventory/Inventory.vue";
import { PlatformInfo } from "@/dto/PlatformInfo";
import { InvoiceTemplate } from "@/templates/InvoiceTemplate";
import InventoryItem from "@/dto/InventoryItem";
import { firestore } from "@/firebaseInit";
import exportFromJSON from "export-from-json";
@Component({
  components: {
    Inventory,
  },
})
export default class Billing extends Vue {
  sellerSearchLoading = false;
  inventoryList: InventoryItem[] = [];
  loading = false;
  searchDisabled = false;
  queryString = "";
  queryStringName = "";
  searchLoading = false;
  excelExportLoading = false;
  previewData: any = "";
  headers: any = [
    { text: "Date Added", value: "auditTrail.createdOn" },

    { text: "Item Code", value: "itemCode" },
    { text: "Item Name", value: "itemName" },
    { text: "Available Quantity", value: "availableQuantity" },
    { text: "Item Price", value: "itemPrice" },
    { text: "Total Tax", value: "totalTax" },

    { text: "Added By", value: "auditTrail.createdBy.email" },
    { text: "Updated By", value: "auditTrail.updatedBy.email" },
    { text: "Actions", value: "actions", width: "100px", sortable: false },
  ];
  editedIndex = 0;
  editedItem: InventoryItem = new InventoryItem();
  dialog = false;

  async mounted() {
    await this.searchProducts();
  }
  async searchProducts() {
    console.log("query:" + this.queryString);
    if (this.queryString !== "" || this.queryStringName !== "") {
      await this.searchQueryString();
    } else {
      await this.getProducts();
    }
  }
  async searchQueryString() {
    try {
      this.searchDisabled = true;
      this.searchLoading = true;
      this.inventoryList = [];
      const baseQuery = firestore
        .collection("Inventory")
        .withConverter(InventoryItem.converter);
      let query: any = baseQuery;
      if (this.queryString) {
        query = query
          .where("itemCode", ">=", this.queryString)
          .where("itemCode", "<=", this.queryString + "\uf8ff");
      }
      if (this.queryStringName) {
        query = query
          .where("itemTag", ">=", this.queryStringName)
          .where("itemTag", "<=", this.queryStringName + "\uf8ff");
      }

      const snapshot = await query.limit(50).get();
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.inventoryList.push(doc.data());
        });
        Vue.toasted.show("Found " + this.inventoryList.length + " items", {
          icon: "face",
          type: "success",
        });
      } else {
        Vue.toasted.show("No results found", { icon: "face", type: "error" });
      }
      this.queryString = "";
      this.queryStringName = "";
    } catch (error) {
      Vue.toasted.show(error.message, { icon: "face", type: "error" });
    } finally {
      this.searchLoading = false;
      this.searchDisabled = false;
    }
  }
  async getProducts() {
    try {
      this.inventoryList = [];
      this.sellerSearchLoading = true;
      const snapshot = await FirestoreService.getDocumentsByOrder(
        "Inventory",
        InventoryItem.converter,
        "auditTrail.createdOn",
        "desc",
        2000
      );
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.inventoryList.push(doc.data());
        });
        Vue.toasted.show("Search complete", { icon: "face", type: "success" });
      } else {
        Vue.toasted.show("No results found", { icon: "face", type: "error" });
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.sellerSearchLoading = false;
    }
  }
  editItem(item: InventoryItem) {
    this.editedIndex = this.inventoryList.indexOf(item);
    this.editedItem = Object.assign({}, item);
    this.$store.dispatch("setInvoicePanelFlag", true);
  }

  async deleteItem(item: InventoryItem) {
    try {
      const res = await Swal.fire({
        title: "Are you sure?",
        text: "You won't be able to revert this!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!",
      });
      if (res.isConfirmed) {
        await FirestoreService.deleteDocument("Inventory", item.itemCode);
        const deleteIndex = this.inventoryList.indexOf(item);

        this.inventoryList.splice(deleteIndex, 1);
        Swal.fire("Deleted!", "Seller has been deleted.", "success");
      }
    } catch (error) {
      Vue.toasted.show(error.message, { icon: "face", type: "error" });
    }
  }
  printItem(item: InventoryItem) {
    const data = InvoiceTemplate.getMessage(item);
    this.previewData = URL.createObjectURL(
      new Blob([data], { type: "text/html" })
    );
    const newWindow = window.open(this.previewData, "_blank");
    if (newWindow) {
      newWindow.onload = () => {
        newWindow.document.getElementsByTagName(
          "html"
        )[0].style.backgroundColor = "whiteSmoke";
      };
    }
  }
  async excelExport() {
    try {
      this.excelExportLoading = true;
      const inventoryList: InventoryItem[] = [];
      const snapshot = await FirestoreService.getDocumentsByOrder(
        "Inventory",
        InventoryItem.converter,
        "auditTrail.createdOn",
        "desc",
        2000
      );
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          inventoryList.push(doc.data());
        });
        Vue.toasted.show("export items complete", {
          icon: "face",
          type: "success",
        });
        exportFromJSON({
          data: inventoryList,
          fileName: "export_" + new Date().toLocaleString(),
          exportType: exportFromJSON.types.csv,
        });
      } else {
        Vue.toasted.show("No results found", {
          icon: "face",
          type: "error",
        });
      }
    } catch (error) {
      Vue.toasted.show(error, {
        icon: "face",
        type: "error",
      });
    } finally {
      this.excelExportLoading = false;
    }
  }
  // async searchQueryString() {
  //   try {
  //     this.searchDisabled = true;
  //     this.searchLoading = true;
  //     this.inventoryList = [];
  //     const snapshot = await FirestoreService.getdocsStartingWithKey(
  //       "Inventory",
  //       this.queryString,
  //       "itemCode",
  //       InventoryItem.converter
  //     );
  //     if (!snapshot.empty) {
  //       snapshot.forEach((doc: any) => {
  //         this.inventoryList.push(doc.data());
  //       });
  //       Vue.toasted.show("Found " + this.inventoryList.length + " items", {
  //         icon: "face",
  //         type: "success",
  //       });
  //     } else {
  //       Vue.toasted.show("No results found", { icon: "face", type: "error" });
  //     }
  //   } catch (error) {
  //     Vue.toasted.show(error.message, { icon: "face", type: "error" });
  //   } finally {
  //     this.searchLoading = false;
  //     this.searchDisabled = false;
  //     this.queryString = "";
  //   }
  // }
}
