import { classToPlain, plainToClass } from "class-transformer";
import { AuditFields } from "./AuditFields";

export class InventoryTransactions {
  txnId: string;
  txnType: string;
  qty: number;
  availableQty: number;
  srNo = 1;
  orderId = "";
  txnIdDate: string;
  constructor() {
    this.txnId = "";
    this.txnType = "";
    this.availableQty = 0;
    this.qty = 0;
    this.txnIdDate = "";
  }
  static converter = {
    toFirestore(
      expense: InventoryTransactions
    ): firebase.firestore.DocumentData {
      return classToPlain(expense);
    },
    fromFirestore(
      snapshot: firebase.firestore.QueryDocumentSnapshot,
      options: firebase.firestore.SnapshotOptions
    ): InventoryTransactions {
      const data = snapshot.data(options)!;
      return plainToClass(InventoryTransactions, data);
    },
  };
}
