import 'es6-shim';
import 'reflect-metadata';
import { v4 as uuidv4 } from 'uuid';

export default class Carousel {
    id: string;
    rank: number;
    header: string;
    content: string;
    link: string;

    constructor() {
        this.rank = 0;
        this.header = "";
        this.content = "";
        this.link = "";
        this.id = uuidv4();
    }

}