import "es6-shim";
import "reflect-metadata";
import { classToPlain, plainToClass, Type } from "class-transformer";
import { AuditFields } from "./AuditFields";
import { v4 as uuidv4 } from "uuid";

export default class InventoryItem {
  id: string;
  itemName: string;
  itemDescription: string;
  itemCode: string;
  itemHsn: string;
  itemSerialNumber: string;
  availableQuantity: number;
  purchaseQuantity: number;
  itemPrice: number;
  isInStock: boolean;
  itemCategory: string;
  auditTrail!: AuditFields;
  supplierId: string;
  supplierName: string;
  GSTRate: number;
  itemCondtion: string;
  itemTag: string;
  orderId: string;
  srNo = 1;
  lastLabelPrinted: number;
  constructor() {
    this.id = uuidv4();
    this.itemName = "";
    this.itemDescription = "";
    this.itemCode = "";
    this.itemHsn = "";
    this.itemSerialNumber = "";
    this.availableQuantity = 0;
    this.itemPrice = 0;
    this.isInStock = true;
    this.itemCategory = "";
    this.supplierId = "";
    this.supplierName = "";
    this.GSTRate = 0;
    this.itemCondtion = "New";
    this.itemTag = "";
    this.purchaseQuantity = 1;
    this.orderId = "";
    this.lastLabelPrinted = 0;
  }

  get total() {
    return this.itemPrice * this.purchaseQuantity;
  }

  static converter = {
    toFirestore(expense: InventoryItem): firebase.firestore.DocumentData {
      return classToPlain(expense);
    },
    fromFirestore(
      snapshot: firebase.firestore.QueryDocumentSnapshot,
      options: firebase.firestore.SnapshotOptions
    ): InventoryItem {
      const data = snapshot.data(options)!;
      return plainToClass(InventoryItem, data);
    },
  };
}
