import { Component, Prop, Vue } from "vue-property-decorator";


import { FirestoreService } from '@/services/FirestoreService';
import CarouselInfoV2 from "@/dto/CarouselnfoV2";
import Carousel from "@/dto/Carousel";
import { storage } from '@/firebaseInit';


@Component
export default class AddCarouselV2 extends Vue {

  loading = false;
  label = "Carousel V2";
  carsoulInfo: CarouselInfoV2 = new CarouselInfoV2();
  selectedImage: any = null;
  contentImage: any = null;
  selectedSideImage = '';
  selectedContentImage = '';
  currentCarousel: Carousel = new Carousel();
  currentSideItem: Carousel = new Carousel();
  uploadSideItemBtnDisabled = true;
  uploadSideItemBtnLoading = false;

  uploadContentBtnDisabled = true;
  uploadContentBtnLoading = false;
  saveItem = false;
  carouselItem = false;
  sideItem = false;
  editId = "";
  deleteId = ""
  dialogDelete = false;
  async mounted() {
    await this.getCarouselDetails()
  }

  async validateForm() {
    if ((this.$refs.addSellerForm as Vue & { validate: () => boolean }).validate()) {
      if (this.currentCarousel.content !== '') {
        this.saveCarousel();
      } else {
        Vue.toasted.show('Please add content image', { icon: 'face', type: 'error' });
      }
    }
  }
  async validateSideForm() {
    if ((this.$refs.addSideItemForm as Vue & { validate: () => boolean }).validate()) {
      if (this.currentSideItem.content !== '') {
        this.saveSideCarousel();
      } else {
        Vue.toasted.show('Please add content image', { icon: 'face', type: 'error' });
      }
    }
  }
  resetForm() {
    this.currentCarousel = new Carousel();
    (this.$refs.addSellerForm as Vue & { reset: () => boolean }).reset();
  }
  resetSideForm() {
    this.currentSideItem = new Carousel();
    (this.$refs.addSideItemForm as Vue & { reset: () => boolean }).reset();
  }
  async deleteItem() {
    try {
      const index = this.carsoulInfo.carousel.map((item) => item.id).indexOf(this.deleteId);
      this.carsoulInfo.carousel.splice(index, 1)
      const snapshot = await FirestoreService.setDocument('HomeScreen', 'CarouselV2', this.carsoulInfo, CarouselInfoV2.converter);
      Vue.toasted.show('Item Deleted Successfully', { icon: 'face', type: 'success' });
      this.dialogDelete = false;

    } catch (error) {
      Vue.toasted.show('Error deleting Carousel item', { icon: 'face', type: 'error' });
      console.log(error)
    }

  }
  displayDeleteModal(item: any) {
    this.deleteId = item;
    this.dialogDelete = true;
  }
  closeDelete() {
    this.dialogDelete = false;
  }
  async saveCarousel() {
    try {
      this.saveItem = true;
      if (this.editId !== '') {
        const cItem: any = this.carsoulInfo.carousel.find(c => c.id === this.editId);
        Object.assign(cItem, this.currentCarousel);
        if (!cItem) {
          Vue.toasted.show('Error in updating item', { icon: 'face', type: 'error' });
          return;
        }
      } else {
        this.carsoulInfo.carousel.push(this.currentCarousel);
      }
      const snapshot = await FirestoreService.setDocument('HomeScreen', 'CarouselV2', this.carsoulInfo, CarouselInfoV2.converter);
      Vue.toasted.show('Added Item Successfully', { icon: 'face', type: 'success' });
      this.selectedImage = null;
      this.contentImage = null;
      this.selectedContentImage = '';
      this.carouselItem = false;
      this.resetForm();
    } catch (error) {
      Vue.toasted.show('Error saving Carousel Details', { icon: 'face', type: 'error' });
      console.log(error)
    }
    finally {
      this.saveItem = false;
    }
  }
  async saveSideCarousel() {
    try {
      this.saveItem = true;
      if (this.editId !== '') {
        const cItem: any = this.carsoulInfo.sideItems.find(c => c.id === this.editId);
        Object.assign(cItem, this.currentSideItem);
        if (!cItem) {
          Vue.toasted.show('Error in updating item', { icon: 'face', type: 'error' });
          return;
        }
      } else {
        this.carsoulInfo.sideItems.push(this.currentSideItem);
      }
      const snapshot = await FirestoreService.setDocument('HomeScreen', 'CarouselV2', this.carsoulInfo, CarouselInfoV2.converter);
      Vue.toasted.show('Added Item Successfully', { icon: 'face', type: 'success' });
      this.selectedImage = null;
      this.selectedSideImage = '';
      this.sideItem = false;
      this.resetSideForm();
    } catch (error) {
      Vue.toasted.show('Error saving Carousel Details', { icon: 'face', type: 'error' });
      console.log(error)
    }
    finally {
      this.saveItem = false;
    }
  }
  previewSideImage(event: any) {
    this.selectedImage = event.target.files[0];
    this.selectedSideImage = URL.createObjectURL(this.selectedImage);
    this.uploadSideItemBtnDisabled = false;
  }
  previewContentImage(event: any) {
    this.contentImage = event.target.files[0];
    this.selectedContentImage = URL.createObjectURL(this.contentImage);
    this.uploadContentBtnDisabled = false;
  }
  addCarouselItem(item: any) {
    console.log(item)
    if (item && item !== '') {
      this.editId = item;
      const cItem: any = this.carsoulInfo.carousel.find(c => c.id === item);
      this.currentCarousel = cItem;
      this.selectedContentImage = this.currentCarousel.content;
      this.carouselItem = true;
    } else {
      this.selectedContentImage = '';
      this.editId = '';
      this.carouselItem = true;
    }
  }

  addSideItem(item: any) {
    console.log(item)
    if (item && item !== '') {
      this.editId = item;
      const cItem: any = this.carsoulInfo.sideItems.find(c => c.id === item);
      this.currentSideItem = cItem;
      this.selectedSideImage = this.currentSideItem.content;
    } else {
      this.selectedSideImage = '';
      this.editId = '';
    }
    this.sideItem = true;
  }
  cancelForm() {
    this.resetForm();
    this.carouselItem = false;
  }
  cancelsideForm() {
    this.resetSideForm();
    this.sideItem = false;
  }
  async getCarouselDetails() {
    try {
      this.loading = true;
      const snapshot = await FirestoreService.getDocument('HomeScreen', 'CarouselV2', CarouselInfoV2.converter);
      if (snapshot !== null) {
        this.carsoulInfo = snapshot.data() as CarouselInfoV2;
      } else {
        Vue.toasted.show('No Saved Details found', { icon: 'face', type: 'error' });
      }

    } catch (error) {
      Vue.toasted.show('Error fetching Carousel Details', { icon: 'face', type: 'error' });
      console.log(error)
    }
    finally {
      this.loading = false;
    }

  }
  getFileExtension(selectedImage: any) {
    return selectedImage.name.substring(selectedImage.name.lastIndexOf(".") + 1);
  }

  async uploadContentImage() {
    try {
      this.uploadContentBtnLoading = true;
      const fileExtension = this.getFileExtension(this.contentImage);

      const metadata = {
        cacheControl: 'public,max-age=172800',
      };
      const resp = await storage.ref(`carousel-v2/content-${this.currentCarousel.id}.` + fileExtension).put(this.contentImage, metadata);
      const url = await resp.ref.getDownloadURL();
      this.currentCarousel.content = url;
      Vue.toasted.show('Image Uploaded Successfully', { icon: 'face', type: 'success' });
    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });
    } finally {
      this.uploadContentBtnLoading = false;
    }

  }
  async uploadSideContentImage() {
    try {
      this.uploadSideItemBtnLoading = true;
      const fileExtension = this.getFileExtension(this.selectedImage);
      const metadata = {
        cacheControl: 'public,max-age=172800',
      };
      const resp = await storage.ref(`carousel-v2/side-${this.currentSideItem.id}.` + fileExtension).put(this.selectedImage, metadata);
      const url = await resp.ref.getDownloadURL();
      this.currentSideItem.content = url;
      Vue.toasted.show('Image Uploaded Successfully', { icon: 'face', type: 'success' });
    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });
    } finally {
      this.uploadSideItemBtnLoading = false;
    }

  }
}
