import { Component, Prop, Vue } from "vue-property-decorator";


import { FirestoreService } from '@/services/FirestoreService';
import CarouselInfoV2 from "@/dto/CarouselnfoV2";

import { UtilityService } from "@/services/UtilityService";

import { Section } from "@/dto/Section";
import ProductSection from "../ProductSection/ProductSection.vue";

@Component({
  components: {
    ProductSection
  }
})
export default class ProductSections extends Vue {

  loading = false;
  label = "Product Section";
  productSections: Section[] = [];
  currProductSection: Section = new Section();
  uploadSideItemBtnDisabled = true;
  uploadSideItemBtnLoading = false;
  saveItem = false;
  sideItem = false;
  editId = "";
  dialogDelete = false;
  deleteId = "";

  addSectionFlag = false;
  async mounted() {
    await this.getSectionsDetails()
  }

  async validateForm() {
    if ((this.$refs.addSectionForm as Vue & { validate: () => boolean }).validate()) {
      this.saveSection();
    }
  }

  resetForm() {
    this.currProductSection = new Section();
    this.addSectionFlag = false;

    (this.$refs.addSectionForm as Vue & { reset: () => boolean }).reset();
  }

  async deleteItem() {
    try {
      const index = this.productSections.map((item) => item.id).indexOf(this.deleteId);
      const snapshot = await FirestoreService.deleteDocument('ProductSections', this.deleteId);
      Vue.toasted.show('Item Deleted Successfully', { icon: 'face', type: 'success' });
      this.productSections.splice(index, 1)

      this.dialogDelete = false;

    } catch (error) {
      Vue.toasted.show('Error deleting Carousel item', { icon: 'face', type: 'error' });
      console.log(error)
    }

  }
  displayDeleteModal(item: any) {
    this.deleteId = item;
    this.dialogDelete = true;
  }
  closeDelete() {
    this.dialogDelete = false;
  }
  async saveSection() {
    try {
      this.saveItem = true;
      this.currProductSection.id = UtilityService.convertToKebabCase(this.currProductSection.name) as string;
      const snapshot = await FirestoreService.setDocument('ProductSections', this.currProductSection.id, this.currProductSection, CarouselInfoV2.converter);
      Vue.toasted.show('Section Added Successfully', { icon: 'face', type: 'success' });
      await this.getSectionsDetails();
      this.resetForm();
    } catch (error) {
      Vue.toasted.show('Error saving Carousel Details', { icon: 'face', type: 'error' });
      console.log(error)
    }
    finally {
      this.saveItem = false;
    }
  }


  addSection() {
    this.currProductSection = new Section();
    this.addSectionFlag = true;
  }


  cancelSectionAddition() {
    this.resetForm();
    this.addSectionFlag = false;
  }

  async getSectionsDetails() {
    try {
      this.loading = true;
      const snapshot = await FirestoreService.getDocuments('ProductSections', Section.converter);
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.productSections.push(doc.data());
        });
        Vue.toasted.show('Search complete', { icon: 'face', type: 'success' });

      } else {
        Vue.toasted.show('No results found', { icon: 'face', type: 'error' });
      }
      // const snapshot = await FirestoreService.getDocuments('ProductSections', 'CarouselV2', CarouselInfoV2.converter);
      // if (snapshot !== null) {
      //   this.carsoulInfo = snapshot.data() as CarouselInfoV2;
      // } else {
      //   Vue.toasted.show('No Saved Details found', { icon: 'face', type: 'error' });
      // }

    } catch (error) {
      Vue.toasted.show('Error fetching Section Details', { icon: 'face', type: 'error' });
      console.log(error)
    }
    finally {
      this.loading = false;
    }

  }


}
